.sideBar {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  padding: 10px;
  border-radius: 20px;

}

.title {
  font-size: 25px;
  margin: auto;
}

.header {
  height: 10vh;
  background-color: green;
  display: -ms-flexbox;
  display: flex;
  border-radius: 20px 20px 0 0;
  color: white;
  -webkit-box-shadow: 4px 4px #D3D3D3;
          box-shadow: 4px 4px #D3D3D3;
}

.filterArea {
  height: 30vh;
  background-color: white;
  -webkit-box-shadow: 4px 4px #D3D3D3;
          box-shadow: 4px 4px #D3D3D3;
}

.listViewArea {
  height: 60vh;
  background-color: white;
  border-radius: 0 0 20px 20px;
  -webkit-box-shadow: 4px 4px #D3D3D3;
          box-shadow: 4px 4px #D3D3D3;
}

footer {
  font-size: 12px;
}

footer p {
  margin: 0;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {

  .listViewArea {
    height: 100%;
  }

  .filterArea {
    height: 250px;
  }

  .header {
    height: 50px;
  }
}



